body {position: relative}
main .shadow,
main {
    min-height: 100%;
    min-height: 100vh;
}
:focus {outline: 0}
#maincontent {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 50;
    width: 100%;
    min-height: 100%;
}
#background {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: hidden;
    background-size: 100% auto;
    background-repeat: no-repeat;
    background-position: center bottom;
}
.btn-round {
    position: relative;
    padding: 3px 5px;
    width: 28px;
    height: 28px;
    border-radius: 50%;
}
.btn-round.btn-lg {
    width: 30px;
    height: 30px;
    padding: 4px 5px;
}
.btn:focus, .btn:active, button:focus, button:active {outline: 0}
.account-menu a, .product-name a,
#catalog-modile a, .nav-offer ul>li>a, .customer a,
.catalog-item a, .label-box a, .discount a,
.unstyled {color: inherit; text-decoration: none !important}
#maincontent .container.shadow {
    background-color: #fff;
     -webkit-box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 1px 5px 0 rgba(0,0,0,0.12), 0 3px 1px -2px rgba(0,0,0,0.2);
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 1px 5px 0 rgba(0,0,0,0.12), 0 3px 1px -2px rgba(0,0,0,0.2);
}
.shadow>.row>.banner {margin-top: -20px}
.col-xl-1, .col-xl-2, .col-xl-3, .col-xl-4 {
    position: relative;
    min-height: 1px;
    padding-left: 15px;
    padding-right: 15px;
    float: left;
}
.cleaner_h20 {clear: both; width: 100%; height: 20px}
#header {
    position: absolute;
    width: 100%;
    z-index: 9999;
    font-size: 18px;    
    white-space: nowrap;
    background-color: #fff;
    box-shadow: 0 2px 15px 0 rgba(0,0,0,.07), 
        0 -3px 9px 0 rgba(0,0,0,.1), 
        0 2px 3px 0 rgba(0,0,0,.2);
}
#header>.row {
    padding-top: 10px;
    padding-bottom: 10px;
}
.require .control-label,
#header .customer>a, .cart-item, .buyer,
#header>div>div {position: relative}
img.logo {max-width: 120px}
.faq .left-menu .btn-link[aria-expanded="true"],
.filter.dropdown>div>.btn-danger[aria-expanded="true"],
.filter .dropdown-body p>a.current, #history .btn.active,
#xs-menu .caret, .more>a:hover, .product-control a.active,
.left-menu .btn-link:hover {color: #60ca00}
#header .phone {padding: 14px 15px}
#header .customer .fa {
    position: relative;
    margin-left: 25px; 
    background-image: url(../img/customer.png);
    background-size: auto 200%;
    top: 8px;
    height: 36px;
    width: 36px;
}
#header .customer>a:first-child {margin-right: 10px}
#header .customer .fa.user {background-position: center top}
#header .customer .fa.loop {background-position: right top}
