#header .customer a:hover>.fa.user {background-position: center bottom}
#header .customer a.active>.fa.loop {background-position: right bottom}
#header .badge {
    position: absolute;
    left: 95%;
    top: -13px;
}
#header div {
    background-color: #fff;
}
.dropdown-basket {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1000;
    display: none;
    float: left;
    padding: 20px 15px;
    text-align: left;
    font-size: 14px;
    background-color: #fff;
    border: 1px solid #eee;
    border-radius: 10px;
    -webkit-box-shadow: 0 6px 12px rgba(0,0,0,0.175);
