}
.dropdown-basket .scroll-area {
    border-top: 1px solid #e5e5e5;
    max-height: 340px;
}
.dropdown-basket .scroll-area .text-center {margin-top: 10px}
.basket-footer {
    border-top: 1px solid #e5e5e5;
}
.basket-footer .cart-total {
    font-size: 18px;
    margin: 10px 0 15px;
}
.basket-item {
    display: flex;
}
.basket-item>div:first-child {width: 17%}
.basket-item-content,
.basket-item-content>div {
    display: flex;
    justify-content: space-between;
}
.basket-item-content {
    flex-direction: column;
    padding-left: 10px;
    width: 83%;
}
.basket-item-content a {color: inherit}
.basket-item-content>div:first-child {
    align-items: flex-start;
    margin-bottom: 6px;
    white-space: normal;
}
.basket-item-content>div:last-child {
    font-size: 16px;
}
.basket-item-content .btn {
    border-color: transparent !important;
    padding: 0;
    margin-left: 10px;
}
.merit>div,
.basket-item {margin: 15px 0}
.basket-item>div:first-child img {max-height: 60px}
.shop-list .product-image, .product-name img {max-height: 80px}
.basket-item>div:first-child img, .product-name img {
    height: auto !important;
    width: auto !important;
}
.basket-history>tbody>tr>td:first-child {
    white-space: normal;
    font-size: 12px;
}
#header .input-group {
    border-radius: 40px;
    border: 2px solid #696969;
    margin: 6px 0;
}
#header .input-group .form-control,
#header .input-group .btn-default {
    height: 36px;
    padding: 4px 15px;
    border-color: transparent;
    box-shadow: none;
}
.tovar-buy .btn-primary,
.left-menu .btn-primary {
    width: 160px;
    font-weight: 500;
    border-width: 2px;
}
a.disabled, .modern-page-navigation>.modern-page-current,
.catalog-item>div:hover .btn:not(.active).btn-primary:not(:hover),
.left-menu .btn-primary[aria-expanded="true"] {
    color: #333;
    border-color: #333;
}
.left-menu .btn-link {
    font-weight: 500;
    position: relative;
    text-decoration: none !important;
    white-space: normal;
    text-align: left;
}
ul.list-check>li:before,
.eco:before, .custom-select:before,
.nav-back .unstyled:before,
input[type=radio] + label:before,
input[type=checkbox] + label:before,
.left-menu .btn-link:before {
    display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.left-menu .btn-link:before {
    content: "\f107";
    font-size: 24px;
    position: absolute;
    left: 0;
    top: 4px;
}
.left-menu .btn-link[aria-expanded="true"]:before {content: "\f106"; top: 2px}
.left-menu ul.list-unstyled {
    margin: 0;
    padding-left: 10px;
    line-height: 1.2;
}
.left-menu ul.list-unstyled>li {margin-top: 15px; position: relative}
.left-menu ul.list-unstyled>li.active>a:before {
    content: '';
    display: inline-block;
    position: absolute;
    left: -10px;
    top: 2px;
    height: 0;
    width: 0;
    border: 5px solid transparent;
    border-left-color: #60ca00;
}
.left-menu ul.list-unstyled>li.active>a {font-weight: 500; color: #333}
#xs-menu ul.nav {
    padding-left: 20px;
    font-size: 20px;
}
#xs-menu .dropdown-menu>li>a {
    font-size: 16px;
    margin: 5px 0;
}
#footer {
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    padding-top: 20px;
    padding-bottom: 20px;
    background-color: #4d4d4d;
    color: #fff;
}
#footer ul {
    margin: 0;
    -moz-column-width: 220px;
    -webkit-column-width: 220px;
    column-width: 220px;
}
#footer ul>li:not(:first-child) {margin: 5px 0}
#footer ul>li>span {font-size: 18px}
#footer a {color: inherit;}
#footer a.unstyled:not(.sopdu) {font-size: 20px; line-height: 2}
#footer .btn-round {
    background-color: #696969;
    width: 40px;
    height: 40px;
    font-size: 18px;
    line-height: 32px;
    margin: 0 5px;
}
#footer .btn-round:hover {background-color: #7b7b7b}
#footer small {color: #ccc}
#footer a.sopdu {
    display: inline-block;
    text-align: left;
    white-space: nowrap;
    width: 210px;
    margin-top: 10px;
}
a.sopdu img {
    width: 34px;
    float: left;
    margin-right: 10px;
}
.list-inline>li {margin-bottom: 5px}
.lineback {
    position: relative;
    width: 100%;
    min-height: 18px;
    text-align: center;
    font-size: 12px;
}
.lineback:before {
    content: '';
    height: 1px;
    background-color: #999;
    position: absolute;
    left: 15px;
    right: 15px;
    top: 50%;
}
.lineback>span {
    position: relative;
    padding: 0 15px;
    background-color: #fff;
    z-index: 2;
    font-weight: 400;
}
.login h5, .more,
.filter.dropdown {margin-top: 20px; margin-bottom: 20px}
.filter.dropdown>div {display: inline}
.tovar-card .tab-pane .btn.pull-right,
.filter.dropdown>div>.btn-danger {
    border-color: transparent !important;
    padding: 0 10px;
