    display: inline-block;
    color: #696969;
    border: solid 1px #696969;
}
.filter .dropdown-body p>a {
    display: inline-block;
    margin: 0 10px;
    font-size: 18px;
    text-decoration: none !important;
}
.filter .dropdown-body ul.list-unstyled {
    -moz-column-width: 175px;
    -webkit-column-width: 175px;
    column-width: 175px;
    margin: 0;
}
.filter .dropdown-body ul.list-unstyled>li {
    position: relative;
    margin-bottom: 5px;
}
.login .radio {
    position: relative;
