    background-color: #aaa;
    border-radius: 5px;
}
.filter .dropdown-body form>.divider:first-of-type,
.feedback h5, .dropdown-basket .h5 {margin-top: 0}
.filter .dropdown-body form>.form-group>input {width: 90px; margin: 0 5px}
