    box-shadow: none !important;
    padding: 4px;
    font-size: 16px;
    color: #000;
}
.count>.btn-round.plus:after,
.count>.btn-round:before {
    content: '';
    background-color: #696969;
    position: absolute;
    height: 2px;
    left: 8px; 
    right: 8px;
    top: 12px;
}
.count>.btn-round.plus:after {
    -webkit-transform: rotate(90deg);
    -moz-transform: rotate(90deg);
    transform: rotate(90deg);
}
.count>.btn-round.plus:hover:after,
.count>.btn-round:hover:before {background-color: #333}
